<template>
  <div>
    <Navbar :user="user"></Navbar>
    <router-view :userData="userData"></router-view>
  </div>
</template>

<script>
import firebase from 'firebase/app';
import Navbar from '../components/Navbar.vue';

export default {
  name: 'Game',
  data() {
    return {
      userData: {},
      user: {},
      statue: {
        start: false,
      },
    };
  },
  components: {
    Navbar,
  },
  methods: {
    LoginOrNot() {
      const vm = this;
      firebase.auth().onAuthStateChanged((userState) => {
        if (userState) {
          firebase.database().ref(`users/${userState.uid}`).on('value', (snapshot) => {
            vm.user.data = snapshot.val();
            vm.user.id = snapshot.key;
          });
        } else {
          vm.user.id = '';
        }
      });
    },
  },
  created() {
    const vm = this;
    vm.$bus.$on('user:name', (val) => {
      vm.userData = val;
    });
    vm.LoginOrNot();
  },
};
</script>

